<template>
  <div class="container">
    <p @click="copy">{{this.$store.getters.PEERID}}</p>
    <textinput placeholder="id партнера" max="300" type="text" @custominput="data=>{this.text = data}"/>
    <div class="button" @click="call">позвонить</div>
  </div>
</template>

<script>
import Textinput from "@/components/textinput.component";
import Checkbox from "@/components/checkbox.component";
import Fileinput from "@/components/fileinput.component";

export default {
  name: "home",
  components: {Fileinput, Checkbox, Textinput},
  data() {
    return {
      text: ''
    }
  },
  mounted() {
  },
  methods: {
    call(){
      console.log('calling')
      this.$router.push(`/room/${this.text}`)
    },
    copy(){
      navigator.clipboard.writeText(this.$store.getters.PEERID)
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  display: flex;
  gap: 24px;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-end;
  p{
    cursor: pointer;
  }
}
</style>
